<!--  -->
<template>
  <div style="width: 100%;
    height: 100%;">
    <div id="chart"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
    };
  },

  components: {},

  computed: {
    line_data(){
      var boatIdArray = this.$store.state.line_data.map(item => {
        return item.boat_id;
      });
      var index = boatIdArray.indexOf(`${this.$store.state.selected_boat.id}`);
      if (index != -1) {
        return this.$store.state.line_data[index];
      } else {
        return [];
      }
    },
    watcher(){
      return JSON.stringify(this.$store.state.line_data);
    }
  },

  watch: {
    watcher(val,oldVal) {
      this.initChart();
    }
  },

  mounted() {
    this.initChart();
  },

  methods: {
    initChart() {
      var _this=this;
      var myChart = this.$echarts.init(document.getElementById("chart"));
      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption({
        title: {
          text: "当前时间段监测pH值"
        },
        xAxis: {
          type: "category",
          data: _this.line_data.dateTime,
        },
        yAxis: {
          type: "value"
        },
         tooltip: {
          trigger: "axis"
        },
        series: [
          {
            data: _this.line_data.pH,
            type: "line"
          }
        ]
      });
    }
  }
};
</script>
<style scoped>
#chart {
  width: 100%;
  height: 100%;
}
</style>