<template>
  <div class="contentbox">
    <div class="content">
      <div class="video"></div>
      <div class="path_draw">
        <div class="remind">
          <p>温馨提示：使用方向键控制小船移动</p>
          <el-switch
            style="display: block"
            v-model="switch_value"
            active-color="#13ce66"
            inactive-color="#13ce66"
            active-text="数据图"
            inactive-text="路径图"
          ></el-switch>
        </div>
        <div class="switch_box">
          <pathdraw v-if="!switch_value"></pathdraw>
          <linechart v-else></linechart>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import pathdraw from "../../components/content/BmapAndEcharts";
import linechart from "../../components/content/LineChart2";
export default {
  data() {
    return {
      switch_value: false,
      websock: null
    };
  },
  created() {
    this.initWebSocket();
  },
  beforeDestroy() {
    this.websocketclose();
  },
  components: { pathdraw, linechart },

  computed: {
    boat() {
      return this.$store.state.selected_boat;
    }
  },

  mounted() {},

  methods: {
    initWebSocket() {
      const wsuri = `ws://127.0.0.1:8001`; //这个地址由后端提供
      this.websock = new WebSocket(wsuri);
      this.$store.commit("setWebSocket", this.websock);
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onopen = this.websocketonopen;
      this.websock.onerror = this.websocketonerror;
      this.websock.onclose = this.websocketclose;
    },
    websocketonopen() {
      //连接建立之后开始监听方向键事件
      this.createWatcher();
      console.log("WebSocket连接成功");
    },
    websocketonerror() {
      //连接建立失败重连
      console.log("WebSocket连接失败，正在重连");
      this.initWebSocket();
    },
    websocketonmessage(e) {
      // const data=JSON.parse(e.data);
      var obj = JSON.parse(e.data);
      var name = obj.name;
      if (name == "direction") {
        console.log(`接收服务器数据:${obj.direction}`);
      } else if (name == "cancel") {
        console.log(`接收服务器数据:cancel ${obj.direction}`);
      } else if (name == "boat_data") {
        console.log(`接收服务器数据,坐标为:${obj.points}`);
        this.$store.commit("setPoints", obj.points);
      } else if (name == "line_data") {
        console.log(`接收服务器数据,时间:${obj.dateTime}数据:${obj.pH}`);
        this.$store.commit("setLineData", obj);
      }
    },
    websocketsend(Data) {
      //数据发送
      console.log(`向服务器发送数据:${Data}`);

      this.websock.send(Data);
    },
    websocketclose(e) {
      //关闭
      this.websock.close();
      console.log("WebSocket连接断开", e);
    },
    createWatcher() {
      var left_flag = true;
      var right_flag = true;
      var up_flag = true;
      var down_flag = true;
      var _this = this;
      $(window).keydown(function(event) {
        if (event.keyCode == 37 && left_flag) {
          // console.log("左");
          let obj = {
            name: "direction",
            direction: "left"
          };
          _this.websocketsend(JSON.stringify(obj));
          left_flag = false;
        }
        if (event.keyCode == 39 && right_flag) {
          // console.log("右");
          let obj = {
            name: "direction",
            direction: "right"
          };
          _this.websocketsend(JSON.stringify(obj));
          right_flag = false;
        }
        if (event.keyCode == 38 && up_flag) {
          // console.log("上");
          let obj = {
            name: "direction",
            direction: "up"
          };
          _this.websocketsend(JSON.stringify(obj));
          up_flag = false;
        }
        if (event.keyCode == 40 && down_flag) {
          // console.log("下");
          let obj = {
            name: "direction",
            direction: "down"
          };
          _this.websocketsend(JSON.stringify(obj));
          down_flag = false;
        }
        // if (event.keyCode == 13) {
        //   var obj = {
        //     name: "boat_data",
        //     boat_id: "1",
        //     points: [
        //       {
        //         coords: [
        //           [120.030551, 30.223874],
        //           [120.030551, 30.223974],
        //           [120.030551, 30.224074],
        //           [120.030251, 30.224074],
        //           [120.030151, 30.224174],
        //           [120.030051, 30.224374],
        //           [120.029951, 30.224274]
        //         ]
        //       }
        //     ]
        //   };
        //   const objToStr = JSON.stringify(obj);
        //   _this.websocketsend(objToStr);
        // }
        if (event.keyCode == 49) {
          var obj = {
            name: "boat_data",
            points: [
              {
                boat_id: "1",
                coords: [[120.030551, 30.223874]]
              }
            ]
          };
          const objToStr = JSON.stringify(obj);
          _this.websocketsend(objToStr);
        }
        if (event.keyCode == 52) {
          var obj = {
            name: "boat_data",
            points: [
              {
                boat_id: "1",
                coords: [[120.030251, 30.224074]]
              }
            ]
          };
          const objToStr = JSON.stringify(obj);
          _this.websocketsend(objToStr);
        }
        if (event.keyCode == 51) {
          var obj = {
            name: "boat_data",
            points: [
              {
                boat_id: "1",
                coords: [[120.030551, 30.224074]]
              }
            ]
          };
          const objToStr = JSON.stringify(obj);
          _this.websocketsend(objToStr);
        }
        if (event.keyCode == 50) {
          var obj = {
            name: "boat_data",
            points: [
              {
                boat_id: "1",
                coords: [[120.030551, 30.223974]]
              }
            ]
          };
          const objToStr = JSON.stringify(obj);
          _this.websocketsend(objToStr);
        }
        if (event.keyCode == 16) {
          var obj = {
            name: "line_data",
            boat_id: "1",
            dateTime: "2019-06-22 08:00:13",
            pH: "7.33"
          };
          const objToStr = JSON.stringify(obj);
          _this.websocketsend(objToStr);
        }
      });
      $(window).keyup(function(event) {
        if (event.keyCode == 37 && !left_flag) {
          // console.log("取消左");
          let obj = {
            name: "cancel",
            direction: "left"
          };
          _this.websocketsend(JSON.stringify(obj));
          left_flag = true;
        }
        if (event.keyCode == 39 && !right_flag) {
          // console.log("取消右");
          let obj = {
            name: "cancel",
            direction: "right"
          };
          _this.websocketsend(JSON.stringify(obj));
          right_flag = true;
        }
        if (event.keyCode == 38 && !up_flag) {
          // console.log("取消上");
          let obj = {
            name: "cancel",
            direction: "up"
          };
          _this.websocketsend(JSON.stringify(obj));
          up_flag = true;
        }
        if (event.keyCode == 40 && !down_flag) {
          // console.log("取消下");
          let obj = {
            name: "cancel",
            direction: "down"
          };
          _this.websocketsend(JSON.stringify(obj));
          down_flag = true;
        }
      });
    }
  }
};
</script>
